@font-face {
	font-family: Suisse Intl;
	src: url('../fonts/suisse_intl_book/suisse_intl_book.eot');
	src: local('suisse_intl_book'), local('../fonts/suisse_intl_book/suisse_intl_book'),
		url('../fonts/suisse_intl_book/suisse_intl_book.eot?#iefix') format('embedded-opentype'),
		url('../fonts/suisse_intl_book/suisse_intl_book.woff') format('woff'),
		url('../fonts/suisse_intl_book/suisse_intl_book.ttf') format('truetype');
	font-weight: 450;
	font-style: normal;
	font-display: swap;
}
